<template>
  <v-card
    height="100%"
  >
    <div
      id="mafs-categoria"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Categoría.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('mafs-categoria')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los cargos maf.</v-card-subtitle>

      <v-card-text>
        <v-responsive>
          <GChart
            :data="chartData"
            :options="chartOptions"
            type="PieChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Categoría
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in categoriaData"
                :key="index"
              >
                <td>{{ parseCategoria(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['mafs', 'mobileSize']),
    chartData () {
      return [
        ['Categoría', 'Cargos'],
        ['MAF PS C', this.categoriaData.mafc],
        ['MAF PS D', this.categoriaData.mafd],
        ['MAF PS E', this.categoriaData.mafe]
      ]
    },
    chartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 40
        },
        colors: this.colors,
        backgroundColor: { fill: 'transparent' },
        height: 350,
        pieHole: '0.2',
        sliceVisibilityThreshold: 0.0001,
        legend: {
          position: 'right',
          alignment: 'center',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    categoriaData () {
      // MAF PS C
      // MAF PS D
      // MAF PS E
      return this.mafs.reduce((acc, cur) => {
        return cur.descripcion_categoria === 'MAF PS C'
          ? { ...acc, mafc: acc.mafc + 1 }
          : cur.descripcion_categoria === 'MAF PS D'
            ? { ...acc, mafd: acc.mafd + 1 }
            : cur.descripcion_categoria === 'MAF PS E'
              ? { ...acc, mafe: acc.mafe + 1 }
              : acc
      },
      {
        mafc: 0,
        mafd: 0,
        mafe: 0
      }
      )
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    parseCategoria (v) {
      return v === 'mafc'
        ? 'MAF PS C'
        : v === 'mafd'
          ? 'MAF PS D'
          : v === 'mafe'
            ? 'MAF PS E'
            : 'No declara'
    }
  }
}
</script>
